    /* TextTyping */
    #TextoutBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 500px;
    margin-top: 50px;
    }

    .text {
    position: relative;
    }

    .text span {
    display: block;
    color: rgba(128, 128, 128, 0.3);
    font-size: 50px;
    font-weight: bolder;
    overflow: hidden;
    }

    .text p {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    color: #FF4081;
    font-size: 50px;
    font-weight: bolder;
    white-space: pre;
    overflow: hidden;
    width: 0;
    transition: width 0.5s ease-in-out;
    }
    /* Tablet (max-width: 1024px) */
    @media (max-width: 1024px) {
    #TextoutBox {
    height: 400px;
    margin-top: 40px;
    }

    .text span,
    .text p {
    font-size: 40px;
    }
    }

    /* Mobile (max-width: 768px) */
    @media (max-width: 768px) {
    #TextoutBox {
    height: 300px;
    margin-top: 30px;
    }

    .text span,
    .text p {
    font-size: 30px;
    }
    }
