        .sp1-sec1 {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 60rem;
        padding: 3rem;
        }

        > div {
        text-align: center;
        font-size: 3.2rem;
        font-weight: 400;
        color: #FF4081;
        }
        

        .sp1-sec1-img-Box-bg {
        display: flex;
        align-items: center;
        position: relative;
        width: 1230px;
        height: 500px;
        overflow: hidden;
        }

        .sp1-sec1-img-Box {
        display: flex;
        position: absolute;
        justify-content: center;
        align-items: center;

        > li {
        width: 20rem;
        height: 20rem;
        border-radius: 10px;
        position: relative;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        &:not(:last-child) {
        margin-right: 6rem;
        }
        }
        }

        /* Background 이미지 클래스 */
        .sp1-mainImg1 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_01.jpg);
        }
        .sp1-mainImg2 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_02.jpg);
        }
        .sp1-mainImg3 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_03.png);
        }
        // 아래 코드는 기존 코드를 유지하기 위한 추가 정의 (마지막 정의가 우선 적용됨)
        .sp1-mainImg4 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_04.png);
        }
        .sp1-mainImg5 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_05.jpg);
        }
        .sp1-mainImg6 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_06.jpg);
        }
        .sp1-mainImg7 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_07.jpg);
        }
        .sp1-mainImg8 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_09.jpg);
        }
        .sp1-mainImg9 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_10.jpg);
        }
        .sp1-mainImg10 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_11.jpg);
        }
        .sp1-mainImg11 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_12.jpg);
        }
        .sp1-mainImg12 {
        background-image: url(https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images_nail/nail_13.jpg);
        }

        /* 슬라이드 버튼 */
        .sp-slide-left,
        .sp-slide-right {
        position: absolute;
        width: 50px;
        height: 50px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        background-color: transparent;
        border: none;
        top: 45%;
        }
        .sp-slide-left {
        position: absolute;
        top: 50%;
        left: 55px; // 원하는 위치로 조정 가능
        transform: translateY(-50%);
        width: 50px;
        height: 50px;
        background: url("https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images/arrow_left.png")
        no-repeat center center;
        background-size: contain;
        cursor: pointer;
        }
        .sp-slide-right {
        position: absolute;
        top: 50%;
        right: 20px; // 원하는 위치로 조정 가능
        transform: translateY(-50%);
        width: 50px;
        height: 50px;
        background: url("https://raw.githubusercontent.com/Lee-kunwoo/nailart/refs/heads/main/images/arrow_right.png")
        no-repeat center center;
        background-size: contain;
        cursor: pointer;
        }  

        /* Tablet (max-width: 1024px) */
        @media (max-width: 1024px) {
        .sp1-sec1 {
        height: 50rem;
        padding: 3rem;
        > div {
        font-size: 2.8rem;
        }
        }
        .sp1-sec1-img-Box-bg {
        width: 100%;
        height: 400px;
        }
        .sp1-sec1-img-Box > li {
        width: 30rem;
        height: 25rem;
        &:not(:last-child) {
        margin-right: 4rem;
        }
        }
        }

        /* Mobile (max-width: 768px) */
        @media (max-width: 768px) {
        .sp1-sec1 {
        height: 40rem;
        padding: 2rem;
        > div {
        font-size: 2.4rem;
        }
        }
        .sp1-sec1-img-Box-bg {
        width: 100%;
        height: 300px;
        }
        .sp1-sec1-img-Box > li {
        width: 25rem;
        height: 20rem;
        &:not(:last-child) {
        margin-right: 2rem;
        }
        }
        }
