.page {
        width: 100vw;
        height: 100vh;
        margin: auto;
        background-color: rgb(80, 76, 133);
      
        h1 {
          text-align: center;
          font-size: 50px;
          font-weight: bold;
          margin: 0;
          padding-top: 20px;
        }
      
        .outBox {
          margin: 50px auto;
          width: 350px;
          height: 450px;
          display: flex;
          justify-content: center; // 가운데 정렬
          align-items: center;
          gap: 30px;
      
          .inBox {
            text-align: center;
            background-color: white;
            border-radius: 5px;
            width: 300px;
            height: 400px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 15px;
      
            img {
              max-width: 200px;
              height: 300px;
              background: no-repeat center center;
              background-size: cover;
              background: cover;
            }
            img:hover {
              width: 300px;
              height: 400px;
              cursor: pointer;
            }
          }
        }
      }
      
      /* Tablet (max-width: 1024px) */
      @media (max-width: 1024px) {
        .page {
          h1 {
            font-size: 40px;
            padding-top: 15px;
          }
      
          .outBox {
            margin: 40px auto;
            width: 300px;
            height: 400px;
            gap: 20px;
            display: flex;
            justify-content: center; // 가운데 정렬
            align-items: center;
      
            .inBox {
              width: 250px;
              height: 350px;
              padding: 10px;
      
              img {
                max-width: 150px;
                height: 250px;
              }
              img:hover {
                width: 250px;
                height: 350px;
              }
            }
          }
        }
      }
      
      /* Mobile (max-width: 768px) */
      @media (max-width: 768px) {
        .page {
          h1 {
            font-size: 30px;
            padding-top: 10px;
          }
      
          .outBox {
            margin: 30px auto;
            width: 260px;
            height: 350px;
            gap: 15px;
            display: flex;
            justify-content: center; // 가운데 정렬
            align-items: center;
      
            .inBox {
              width: 220px;
              height: 300px;
              padding: 8px;
      
              img {
                max-width: 130px;
                height: 200px;
              }
              img:hover {
                width: 220px;
                height: 300px;
              }
            }
          }
        }
      }
      